:root {
  /* .................................................FONT */
  --main-font: 'Open Sans', sans-serif;
  --secondary-font: 'Raleway', sans-serif;
  /* .....................................Text-Color */
  --primary-text-color: #303030;
  --secondary-text-color: #ffffff;
  --thirth-text-color: #555555;
  --accent-text-color: #ff6c00;
  --accent-secondary-text-color: #ffffff;
  /* ........................................BCG */
  --primary-bcg-color: #ffffff;
  --secondary-bcg-color: #f7f7f7;
  --text-price-contacts-color: #9da4bd;
  --dark-bcg: #191c26;

  /* ...................................grid items */
  --items: 1;
  --indent: 30px;
}
/*..................................................Общее .............................................*/
html {
  scroll-behavior: smooth;
}

body {
  font-style: normal;
  font-family: var(--main-font);
  font-weight: 400;
  font-size: 11px;
  line-height: 1.37;
  letter-spacing: 0.02em;

  background-color: var(--primary-bcg-color);
  color: var(--primary-text-color);
}
h1,
h2,
h3,
h4,
h4,
h6,
p {
  margin: 0;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}
address {
  font-style: normal;
}
.link,
.socials-link {
  text-decoration: none;
  color: currentColor;
}
.list {
  list-style: none;
  padding: 0;
  margin: 0;
}
/* .........................................Сітка для колекції з елементів */
.grid {
  display: flex;
  flex-wrap: wrap;

  gap: var(--indent);
}
.grid-item {
  flex-basis: calc((100% - var(--indent) * (var(--items) - 1)) / var(--items));
}
.container {
  width: 1200px;

  margin-left: auto;
  margin-right: auto;

  padding-left: 15px;
  padding-right: 15px;

  /* outline: 2px solid orangered; */
}
.section {
  padding-top: 130px;
  padding-bottom: 130px;
}
.flex {
  display: flex;
  align-items: center;
}
/* .................................................Header ...................*/
.container.header {
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 1;
}
.header,
.hero {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  background-color: rgba(25, 28, 38, 0.2);
}
.logo {
  margin-right: 71px;
}
.logo::after {
  content: '';
  margin-top: 22px;
  display: block;
  width: 100px;
  height: 10px;
  background-color: #c4c4c4;
}
.navigation-item {
  margin-right: 40px;
}
.navigation-item:last-child {
  margin-right: 0;
}
.navigation-link {
  position: relative;
  color: var(--secondary-text-color);
}
.navigation-link:hover,
.navigation-link:focus {
  color: var(--accent-text-color);
}
.navigation-link.current::after {
  position: absolute;
  content: '';
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: var(--accent-text-color);
}
/* .............................................Контакти................................ */
.header-group {
  margin-left: auto;
}
.navigation-link,
.header-phone {
  display: block;

  padding-top: 52px;
  padding-bottom: 52px;

  font-weight: 600;
  font-size: 12px;

  line-height: 1.33;
  letter-spacing: 0.1em;

  color: var(--secondary-text-color);
}
.header-phone {
  margin-right: 40px;
  text-transform: uppercase;
}
.header-phone:hover,
.header-phone:focus {
  color: var(--accent-text-color);
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.3);
}
.modal-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: yellow;
  width: 500px;
  height: 400px;
}
.modal-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  cursor: pointer;
  border-radius: 50%;
  border: 1px solid var(--accent-text-color);
  background-color: transparent;
}
.modal-icon {
  display: flex;
  justify-content: center;
  align-items: center;
}
/* ......................................................ALL BUTTON */
.btn {
  padding: 12px 20px;
  max-width: 200px;
  min-width: 160px;
  min-height: 40px;

  font-family: inherit;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.33;
  letter-spacing: 0.09em;
  text-transform: uppercase;

  border: 1px solid var(--accent-text-color);
  border-radius: 25px;
  background-color: transparent;
  cursor: pointer;
}
.btn:hover,
.btn:focus {
  color: var(--accent-secondary-text-color);
  background-color: var(--accent-text-color);
}
.btn-mode-light {
  color: var(--primary-text-color);
}
.btn-mode-dark {
  color: var(--accent-secondary-text-color);
}
.slider-btn {
  width: 60px;
  height: 24px;
  padding: 0;

  font-weight: 600;
  text-align: right;
  letter-spacing: 0.1em;

  color: var(--secondary-text-color);
  background-color: transparent;
  border: none;
  border-bottom: 1px solid var(--accent-secondary-text-color);
  cursor: pointer;
}
.slider-btn-disable {
  margin-right: 26px;

  color: rgba(157, 164, 189, 0.6);
  border-color: rgba(157, 164, 189, 0.6);
}
.list-button {
  position: absolute;
  right: 0;
  bottom: -40%;
}
/* .................................................Hero........................................ */
.hero {
  height: 680px;
  background-image: url('./images/hero-overlay.jpg'),
    linear-gradient(to right, #fff 12%, #191c26 12%);
  background-size: 1125px 680px, contain;
  background-position: top right, center;
  background-repeat: no-repeat;
  height: 680px;
}
.hero .container {
  position: relative;
  display: flex;
}
.hero-socials-container {
  flex-basis: 100px;
  display: flex;
  align-items: center;
}
.socials-list {
  transform: translateY(400px) rotate(-90deg);
  transform-origin: top left;
}
.hero-group {
  width: 476px;
  padding-top: 272px;
}
.hero .slider-pagination {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  width: 18px;
}
.slider-item {
  width: 8px;
  height: 2px;
  background-color: #9da4b4;
  margin-bottom: 12px;
}
.slider-item:last-child {
  margin-bottom: 0;
}
.slider-item.active {
  width: 100%;
  background-color: white;
}
.hero-group .uppertitle {
  margin-bottom: 20px;
  font-weight: 600;
  line-height: 1.36;
  letter-spacing: 0.1em;
  text-transform: uppercase;

  color: rgba(157, 164, 189, 0.6);
}
.hero .item:last-child {
  margin-right: 0;
}
.hero .link {
  margin-right: 40px;
  font-weight: 600;
  color: var(--primary-text-color);
}
.hero .link:hover,
.hero .link:focus {
  color: var(--accent-text-color);
}
.hero .main-title {
  margin-bottom: 20px;

  font-weight: 700;
  font-size: 72px;
  line-height: 1.18;
  letter-spacing: 0.05em;

  color: var(--accent-secondary-text-color);
}
.hero-group .subtitle {
  font-weight: 400;
  font-size: 18px;
  line-height: 1.67;
  color: var(--text-price-contacts-color);
}
.hero-socials-container .list {
  display: flex;
  font-weight: 600;
  color: #303030;
}
.header-btn {
  animation: swing 6s ease-in-out infinite forwards;
}

@keyframes swing {
  0% {
    transform: rotate(6deg);
  }
  4% {
    transform: rotate(-6deg);
  }
  8% {
    transform: rotate(3deg);
  }
  12% {
    transform: rotate(-3deg);
  }
  16% {
    transform: rotate(0);
  }
}

/* modal */
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);

  transition: opacity 250ms cubic-bezier(0.075, 0.82, 0.165, 1),
    visibility 250ms cubic-bezier(0.075, 0.82, 0.165, 1);
}
.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  /* transform: translate(-50%, -50%); */
  width: 500px;
  background-color: #fff;
  border-radius: 20px;

  transition: transform 300ms ease-in-out 200ms, opacity 300ms ease-in-out 200ms;
}

/* початковий стан елемента перед анімацією */
.backdrop.is-hidden .modal-wrapper {
  transform: translate(-50%, -50%) scale(0.4);
  opacity: 0;
}

/* кінцевий стан елемента після анімації */
.backdrop .modal-wrapper {
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
}

.modal-btn {
  position: absolute;
  top: 10px;
  right: 10px;

  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  padding: 0;

  border: 1px solid #ff6c00;
  border-radius: 50%;
  background-color: transparent;

  cursor: pointer;
}
/* Modal form */

