/* .........................................Footer section */
.footer {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;

  padding-top: 56px;
  padding-bottom: 56px;
  background-color: var(--secondary-bcg-color);
}
.socials-list {
  margin-left: auto;
}
.socials-list .item {
  margin-right: 40px;
}
.socials-list .item:last-child {
  margin-right: 0;
}
.socials-list::before {
  content: '';
  display: block;
  margin-right: 40px;
  width: 60px;
  height: 1px;
  background-color: var(--primary-text-color);
}
.footer .copyright {
  font-weight: 600;
  color: var(--text-price-contacts-color);
}
.footer .link {
  font-weight: 600;
}
.footer .link:hover,
.footer .link:focus {
  color: var(--accent-text-color);
}
.copiright {
  font-weight: 600;
  color: var(--text-price-contacts-color);
}
/* Ryjgrf */
.scroll-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 50px;
  height: 50px;
  background-color: rgba(255, 108, 0, 1);
  border: none;
  border-radius: 50%;
  cursor: pointer;
}
.scroll-icon {
  fill: #fff;
}
