:root {
  --main-font: "Open Sans", sans-serif;
  --secondary-font: "Raleway", sans-serif;
  --primary-text-color: #303030;
  --secondary-text-color: #fff;
  --thirth-text-color: #555;
  --accent-text-color: #ff6c00;
  --accent-secondary-text-color: #fff;
  --primary-bcg-color: #fff;
  --secondary-bcg-color: #f7f7f7;
  --text-price-contacts-color: #9da4bd;
  --dark-bcg: #191c26;
  --items: 1;
  --indent: 30px;
}

html {
  scroll-behavior: smooth;
}

body {
  font-style: normal;
  font-family: var(--main-font);
  letter-spacing: .02em;
  background-color: var(--primary-bcg-color);
  color: var(--primary-text-color);
  font-size: 11px;
  font-weight: 400;
  line-height: 1.37;
}

h1, h2, h3, h4, h4, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

address {
  font-style: normal;
}

.link, .socials-link {
  color: currentColor;
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.grid {
  gap: var(--indent);
  flex-wrap: wrap;
  display: flex;
}

.grid-item {
  flex-basis: calc((100% - var(--indent) * (var(--items)  - 1) ) / var(--items) );
}

.container {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.section {
  padding-top: 130px;
  padding-bottom: 130px;
}

.flex {
  align-items: center;
  display: flex;
}

.container.header {
  width: 100vw;
  z-index: 1;
  position: fixed;
  top: 0;
}

.header, .hero {
  max-width: 1280px;
  background-color: rgba(25, 28, 38, .2);
  margin-left: auto;
  margin-right: auto;
}

.logo {
  margin-right: 71px;
}

.logo:after {
  content: "";
  width: 100px;
  height: 10px;
  background-color: #c4c4c4;
  margin-top: 22px;
  display: block;
}

.navigation-item {
  margin-right: 40px;
}

.navigation-item:last-child {
  margin-right: 0;
}

.navigation-link {
  color: var(--secondary-text-color);
  position: relative;
}

.navigation-link:hover, .navigation-link:focus {
  color: var(--accent-text-color);
}

.navigation-link.current:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--accent-text-color);
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header-group {
  margin-left: auto;
}

.navigation-link, .header-phone {
  letter-spacing: .1em;
  color: var(--secondary-text-color);
  padding-top: 52px;
  padding-bottom: 52px;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.33;
  display: block;
}

.header-phone {
  text-transform: uppercase;
  margin-right: 40px;
}

.header-phone:hover, .header-phone:focus {
  color: var(--accent-text-color);
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, .3);
  position: fixed;
  top: 0;
  left: 0;
}

.modal-wrapper {
  width: 500px;
  height: 400px;
  background-color: #ff0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  border: 1px solid var(--accent-text-color);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal-icon {
  justify-content: center;
  align-items: center;
  display: flex;
}

.btn {
  max-width: 200px;
  min-width: 160px;
  min-height: 40px;
  letter-spacing: .09em;
  text-transform: uppercase;
  border: 1px solid var(--accent-text-color);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 25px;
  padding: 12px 20px;
  font-family: inherit;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.33;
}

.btn:hover, .btn:focus {
  color: var(--accent-secondary-text-color);
  background-color: var(--accent-text-color);
}

.btn-mode-light {
  color: var(--primary-text-color);
}

.btn-mode-dark {
  color: var(--accent-secondary-text-color);
}

.slider-btn {
  width: 60px;
  height: 24px;
  text-align: right;
  letter-spacing: .1em;
  color: var(--secondary-text-color);
  border: none;
  border-bottom: 1px solid var(--accent-secondary-text-color);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  padding: 0;
  font-weight: 600;
}

.slider-btn-disable {
  color: rgba(157, 164, 189, .6);
  border-color: rgba(157, 164, 189, .6);
  margin-right: 26px;
}

.list-button {
  position: absolute;
  bottom: -40%;
  right: 0;
}

.hero {
  height: 680px;
  height: 680px;
  background-image: url("hero-overlay.0f7bee29.jpg"), linear-gradient(to right, #fff 12%, #191c26 12%);
  background-position: 100% 0, center;
  background-repeat: no-repeat;
  background-size: 1125px 680px, contain;
}

.hero .container {
  display: flex;
  position: relative;
}

.hero-socials-container {
  flex-basis: 100px;
  align-items: center;
  display: flex;
}

.socials-list {
  transform-origin: 0 0;
  transform: translateY(400px)rotate(-90deg);
}

.hero-group {
  width: 476px;
  padding-top: 272px;
}

.hero .slider-pagination {
  width: 18px;
  flex-direction: column;
  align-items: flex-end;
  display: flex;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.slider-item {
  width: 8px;
  height: 2px;
  background-color: #9da4b4;
  margin-bottom: 12px;
}

.slider-item:last-child {
  margin-bottom: 0;
}

.slider-item.active {
  width: 100%;
  background-color: #fff;
}

.hero-group .uppertitle {
  letter-spacing: .1em;
  text-transform: uppercase;
  color: rgba(157, 164, 189, .6);
  margin-bottom: 20px;
  font-weight: 600;
  line-height: 1.36;
}

.hero .item:last-child {
  margin-right: 0;
}

.hero .link {
  color: var(--primary-text-color);
  margin-right: 40px;
  font-weight: 600;
}

.hero .link:hover, .hero .link:focus {
  color: var(--accent-text-color);
}

.hero .main-title {
  letter-spacing: .05em;
  color: var(--accent-secondary-text-color);
  margin-bottom: 20px;
  font-size: 72px;
  font-weight: 700;
  line-height: 1.18;
}

.hero-group .subtitle {
  color: var(--text-price-contacts-color);
  font-size: 18px;
  font-weight: 400;
  line-height: 1.67;
}

.hero-socials-container .list {
  color: #303030;
  font-weight: 600;
  display: flex;
}

.header-btn {
  animation: swing 6s ease-in-out infinite forwards;
}

@keyframes swing {
  0% {
    transform: rotate(6deg);
  }

  4% {
    transform: rotate(-6deg);
  }

  8% {
    transform: rotate(3deg);
  }

  12% {
    transform: rotate(-3deg);
  }

  16% {
    transform: rotate(0);
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .3);
  transition: opacity .25s cubic-bezier(.075, .82, .165, 1), visibility .25s cubic-bezier(.075, .82, .165, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-wrapper {
  width: 500px;
  background-color: #fff;
  border-radius: 20px;
  transition: transform .3s ease-in-out .2s, opacity .3s ease-in-out .2s;
  position: absolute;
  top: 50%;
  left: 50%;
}

.backdrop.is-hidden .modal-wrapper {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.4);
}

.backdrop .modal-wrapper {
  opacity: 1;
  transform: translate(-50%, -50%)scale(1);
}

.modal-btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #ff6c00;
